import React from 'react';

const Card=(props)=>{
    return(
        <div className='bg-light-green br3 pa3 ma2 grow dib bw2 shadow-5 tc '> 
           
            <img  alt='pic' src={`https://robohash.org/${props.id}?size=200x200`}/>
            <div>
          
               <h2>{props.name}</h2>
                <p>{props.email}</p>
        </div>
        </div>
    );
}
 export default Card;
 